import Cookie from "js-cookie";
import { Base64 } from "js-base64";

import { ValidationObserver, ValidationProvider } from "vee-validate";

export default {
	middleware: ["session-control", "admin"],
	layout: "admin",
	components: {
		ValidationObserver,
		ValidationProvider,
	},
	data() {
		return {
			data: {
				name: null,
				phone: null,
				email: null,
				facebook: null,
				twitter: null,
				instagram: null,
				youtube: null,
				linkedin: null,
				img_url: null,
				department: null,
				hospitalName: null,
				address: null,
				tc: null,
				work_phone: null,
				work_phone_2: null,
			},
			siteSettings: this.$store.getters.siteSettings,
			userData: !this.isEmpty(Cookie.get("userData"))
				? JSON.parse(Base64.decode(Cookie.get("userData")))
				: null,
		};
	},
	validate({ params }) {
		return params.id !== null ? params.id : null;
	},
	async asyncData({ params, error, $axios }) {
		try {
			const { data } = await $axios.get(
				process.env.apiBaseUrl + "panel/doctors/update/" + params.id
			);

			return data;
		} catch (e) {
			error({ message: "Diyetisyen Bilgisi Bulunamadı.", statusCode: 404 });
		}
	},
	methods: {
		isEmpty(obj) {
			if (typeof obj == "number") return false;
			else if (typeof obj == "string") return obj.length == 0;
			else if (Array.isArray(obj)) return obj.length == 0;
			else if (typeof obj == "object")
				return obj == null || Object.keys(obj).length == 0;
			else if (typeof obj == "boolean") return false;
			else return !obj;
		},
		editDieticians() {
			let formData = new FormData(this.$refs.dieticiansForm);
			this.$axios
				.post(
					process.env.apiBaseUrl + "panel/doctors/update/" + this.data._id,
					formData,
					{
						json: true,
						withCredentials: false,
						mode: "no-cors",
						headers: {
							"Access-Control-Allow-Origin": "*",
							"Access-Control-Allow-Headers":
								"Origin, Content-Type, X-Auth-Token, Authorization",
							"Access-Control-Allow-Methods":
								"GET, POST, PATCH, PUT, DELETE, OPTIONS",
							"Access-Control-Allow-Credentials": true,
							"Content-Type":
								"multipart/form-data; boundary=" + formData._boundary,
							Authorization: "Bearer " + this.userData.api_token,
						},
					}
				)
				.then((response) => {
					if (response.data.success) {
						this.$izitoast.success({
							title: response.data.title,
							message: response.data.msg,
							position: "topCenter",
						});
						setTimeout(() => {
							this.$router.go(decodeURIComponent("/panel/dieticians"));
						}, 2000);
					} else {
						this.$izitoast.error({
							title: response.data.title,
							message: response.data.msg,
							position: "topCenter",
						});
					}
				});
		},
	},
};
